* {
    box-sizing: border-box;
}

html {
    font-family: "Karla", serif;
    background-color: #d2dfd6;
}

body {
    margin: 0;
}

html,
body,
#root {
    height: 100%;
}


h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Playfair Display", serif;
}

h1 {
    font-size: 3.5rem;
}

h2 {
    font-size: 3rem;
    font-weight: 400;
}

h3 {
    font-size: 2.6rem;
}
p {
    font-size: 1.2rem;
    line-height: 1.3;
}

.julianne_welcome {
    height: 100%;
    max-height: 1000px;
    background-image: url(./presentation/imgs/julianne-hero.jpg);
    background-size: cover;
    position: relative;
}

nav {
    position: fixed;
    background-color: rgba(255, 255, 255, 0.6);
    width: 100%;
    z-index: 1;
}

nav .blur {
    filter: blur(20px);
}

nav ul {
    padding: 0;
    margin: 1rem 0;
    list-style: none;
}

nav ul li {
    margin: 0 4rem;
}

nav ul li a {
    color: black;
    text-decoration: none;
    font-size: 1.5em;
    font-family: "Playfair Display", serif;
}

footer {
    background-color: #03506b;
    padding: 3rem 0;
}

footer h2 {
    text-align: center;
    margin: 0;
    font-size: 2rem;
}

.blue {
    background-color: #03506b;
}

.hero_text {
    position: absolute;
    top: 50%;
    transform: translate(50%, 30%);
}

.hero_text h1,
.hero_text h3 {
    line-height: 1.5;
    margin: 0;
    font-weight: 400;
}

.julianne_biography,
.julianne_contact {
    position: relative;
}

.heading_centered {
    position: absolute;
    top: 50%;
    transform: translate(50%, 50%);
}

.split {
    width: 50%;
    max-width: 50%;
    display: inline-block;
}

.right {
    float: right;
}

.container {
    display: flex;
}

.row {
    flex-direction: row;
}

.column {
    flex-direction: column;
}

.justify-centered {
    justify-content: center;
}

.justify-start {
    justify-content: flex-start;
}

.align-items-center {
    align-items: center;
}

.align-items-stretch {
    align-items: stretch;
}

.block-padding {
    padding: 3rem;
}

.red {
    background-color: #fecac1;
}

.julianne_performances {
    background-color: #03506b;
    padding: 3rem 0 3rem 3rem;
}

.white {
    color: white;
}

.performance_heading {
    margin: 0;
}

.performance_blocks {
    padding: 0;
    list-style: none;
    width: 100%;
    overflow: auto;
    white-space: nowrap;
    margin-top: 3rem;
}

.performance_block {
    min-height: 500px;
    background: #f5f4ca;
    width: 250px;
    margin-right: 4rem;
    float: left;
}

.contact_block {
    margin-bottom: 2rem;
}

.contact_block:last-child {
    margin-bottom: 0;
}

.contact_block h3 {
    font-size: 2.5rem;
    font-weight: 400;
    margin: 0;
}

.contact_block p {
}
